import { defineComponent, ref } from "vue";
export default defineComponent({
  name: 'textBox',

  data() {
    return {
      url_base: "http://101.34.30.52:5098/todo/",
      todo_list: [],
      new_todo: ''
    };
  },

  mounted() {
    this.fetchTodoList();
  },

  methods: {
    fetchTodoList() {
      fetch(`${this.url_base}get`).then(res => {
        return res.json();
      }).then(res => {
        this.todo_list = res.todos;
      });
    },

    fetchAddTodo() {
      fetch(`${this.url_base}add?add_todo=${this.new_todo}`).then(res => {
        console.log(res.json);
      });
      this.fetchTodoList();
    }

  }
});